

.login-container{
    margin-top: 15%;
}

.login-form-1{
    border-radius: 5px;
    box-shadow: 0 5px 8px 0 rgba(0, 0, 0, 0.2), 0 9px 26px 0 rgba(0, 0, 0, 0.19);
    padding: 1%;
    max-width: 25%;
    height: 37.5vh;
}

.login-form-1 h3{
    text-align: center;
    color: #333;
}

.login-form-2{
    border-radius: 5px;
    box-shadow: 0 5px 8px 0 rgba(0, 0, 0, 0.2), 0 9px 26px 0 rgba(0, 0, 0, 0.19);
    padding: 1%;
    background: #0062cc;
    max-width: 25%;
    height: 55vh;
}

.login-form-2 h3{
    text-align: center;
    color: #fff;
}

.login-container form{
    padding: 8%;
}

.btnSubmit{
    width: 50%;
    border-radius: 1rem;
    padding: 1.5%;
    border: none;
    cursor: pointer;
}

.login-form-1 .btnSubmit{
    color: #fff;
    background-color: #0062cc;
}

.login-form-2 .btnSubmit{
    background-color: #fff;
    color: #0062cc;
}

.login-form-2 .ForgetPwd{
    color: #fff;
    font-weight: 600;
    text-decoration: none;
}

.login-form-1 .ForgetPwd{
    color: #0062cc;
    font-weight: 600;
    text-decoration: none;
}

.form-group {
    text-align: right;
}